body,
html {
	margin: 0;
	padding: 0;
	background-color: whitesmoke !important;
	font-family: Arial, Helvetica, sans-serif !important;
}
#con {
	padding-top: 17.333vw;
	display: flex;
	justify-content: center;
	flex-direction: column;
}
.txt-r {
	font-size: 3.5vw;
	position: absolute;
	font-weight: bolder;
	color: #d4d4d4;
	transform: translateX(15vw) translateY(-3vw);
}
.txt-left-rot {
	font-size: 3.5vw;
	position: absolute;
	font-weight: bolder;
	color: #d4d4d4;
	transform: rotate(270deg) translateX(-15vw) translateY(-45vw);
}
.p-box1 {
	position: absolute;
	height: 300px;
	width: 14vw;
	transform: translateX(-24vw) translateY(6vw);
}
.p-heading {
	font-size: 21px;
	font-weight: bold;
	color: #ebb800;
}
.heading-l {
	text-align: end;
}
.heading-r {
	text-align: start;
}
.p-heading::after {
	content: "";
	width: 100%;
	height: 1.5px;
	border-radius: 5rem;
	top: 35px;
	position: absolute;
	left: 50%;
	transform: translateX(-50%);
	background: #9e9e9e;
}
.para {
	font-size: 12px;
	color: #b2b2b2;
	line-height: 20px;
	font-weight: 550;
}
.para-l {
	text-align: end;
}
.para-r {
	text-align: start;
}
img {
	width: 25vw;
}

.p-box2 {
	position: absolute;
	height: 300px;
	width: 14vw;
	transform: translateX(27vw) translateY(8vw);
}
.imgCon {
	display: flex;
	justify-content: center;
}
.shadeCirCon {
	display: flex;
	justify-content: center;
	height: 13vw;
	z-index: 10;
}
.shadeCir {
	margin-top: 35px;
	position: absolute;
	width: 9.4vw;
	height: 9.4vw;
	text-align: center;
	color: #fff;
	border: 0.7vw solid #e9ecef;
	border-radius: 100%;
	background-color: #e9e3e3;
}
.lineCon {
	display: flex;
	justify-content: center;
}
.line {
	contain: "";
	width: 0.4vw;
	height: 16vw;
	background-color: #ececec;
	border-radius: 10px;
}
.txt-l {
	font-size: 3.5vw;
	position: absolute;
	font-weight: bolder;
	color: #d4d4d4;
	transform: translateX(-13.4vw) translateY(-4.1vw);
}
.txt-right-rot {
	font-size: 3.5vw;
	position: absolute;
	font-weight: bolder;
	color: #d4d4d4;
	transform: rotate(270deg) translateX(-26vw) translateY(45.65vw);
}
.inCircle {
	height: 20vw;
	width: 20vw;
	border-radius: 50%;
	background-color: #262521;
	display: flex;
	align-items: center;
	text-align: center;
	flex-direction: column;
	transform: translateY(0.59vw);
}
.inCirclehead,
.inCirclebody {
	padding: 0 1.5vw;
	transform: translateY(4.69vw);
}
.inCirclebody {
	font-size: 11.5px;
	color: #b2b2b2c6;
	line-height: 1.3vw;
	font-weight: 550;
}
.inCirclehead {
	font-size: 1.7vw;
	font-weight: bolder;
	color: #ebb800;
}
.sqshape {
	display: flex;
	justify-content: center;
}
.sq {
	background-color: #e9e3e3;
	border: 0.533vw solid #e9ecef;
	width: 12.5vw;
	height: 10vw;
	border-radius: 35px 35px 35px 5px;
	transform: translateX(5.99vw) translateY(-2.1vw);
	z-index: 5;
}
.sqshape p {
	position: absolute;
	width: 11.5vw;
	height: 50px;
	font-size: 0.8vw;
	color: #b2b2b2c6;
	line-height: 1.1vw;
	font-weight: 550;
	transform: translateX(22.5vw) translateY(1vw);
}
.af_fir .txt-r {
	transform: translateX(16.5vw) translateY(-5vw);
}

#lastp {
	font-size: 3.5vw;
	text-align: center;
	font-weight: bolder;
	color: #d4d4d4;
}

@media (max-width: 980px) {
	.p-heading {
		font-size: 2vw;
	}
	.para {
		font-size: 1.2vw;
		line-height: 2vw;
	}
	.p-heading::after {
		top: 3.6vw;
		height: 0.147vw;
	}
	.txt-r {
		transform: translateX(18vw) translateY(-4vw);
	}
	.txt-l {
		transform: translateX(-19vw) translateY(-6vw);
	}
	.inCirclebody {
		font-size: 1vw;
		line-height: 1.3vw;
	}
	.af_fir .txt-r {
		transform: translateX(20vw) translateY(-6.5vw);
	}
}
